body {
  font-family: 'Open Sans', serif;
  padding-top: 54px;
  color: #868e96;
}

@media (min-width: 992px) {
  body {
      padding-top: 0;
      padding-left: 17rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Saira Extra Condensed', serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #343a40;
}

h1 {
  font-size: 6rem;
  line-height: 5.5rem;
}

h2 {
  font-size: 3.5rem;
}

.subheading {
  /* text-transform: uppercase; */
  font-weight: 500;
  font-family: 'Saira Extra Condensed', serif;
  font-size: 1.35rem;
}

.list-social-icons a {
  color: #00507f;
}

.list-social-icons a:hover {
  color: #0068a5;
}

.list-social-icons a .fa-lg {
  font-size: 1.75rem;
}

.list-icons {
  font-size: 3rem;
}

.list-icons .list-inline-item i:hover {
  color: #0068a5;
}

#sideNav .navbar-nav .nav-item .nav-link {
  font-weight: 600;
  text-transform: uppercase;
}

@media (min-width: 992px) {
  #sideNav {
      text-align: center;
      position: fixed;
      top: 0;
      left: 0;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      width: 17rem;
      height: 100vh;
  }
  #sideNav .navbar-brand {
      display: -ms-flexbox;
      display: flex;
      margin: auto auto 0;
      padding: 0.5rem;
  }
  #sideNav .navbar-brand .img-profile {
      max-width: 10rem;
      max-height: 10rem;
      border: 0.5rem solid rgba(255, 255, 255, 0.2);
  }
  #sideNav .navbar-collapse {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: start;
          align-items: flex-start;
      -ms-flex-positive: 0;
          flex-grow: 0;
      width: 100%;
      margin-bottom: auto;
  }
  #sideNav .navbar-collapse .navbar-nav {
      -ms-flex-direction: column;
          flex-direction: column;
      width: 100%;
  }
  #sideNav .navbar-collapse .navbar-nav .nav-item {
      display: block;
  }
  #sideNav .navbar-collapse .navbar-nav .nav-item .nav-link {
      display: block;
  }
}

section.resume-section {
  border-bottom: 1px solid #dee2e6;
  padding-top: 5rem !important;
  padding-bottom: 5rem !important;
}

.resume-date{
  padding-left: 0.5em;
}

section.resume-section .resume-item .resume-date {
  min-width: none;
}

@media (min-width: 768px) {
  section.resume-section {
      min-height: 100vh;
  }
  section.resume-section .resume-item .resume-date {
      min-width: 18rem;
  }
}

@media (min-width: 992px) {
  section.resume-section {
      padding-top: 3rem !important;
      padding-bottom: 3rem !important;
  }
}

.bg-primary {
  background-color: #0068a5 !important;
}

.text-primary {
  color: #0068a5 !important;
}

a {
  color: #0068a5;
}

a:hover,
a:focus,
a:active {
  color: #0081cc;
}

.mb-0,
.my-0 {
  text-transform: unset;
}

.img-text {
  display: block;
  font-size: 0.3em;
  text-align: center;
}

.img-span {
  display: inline-block;
  padding-right: 0.5em;
  text-align: center;
}


